// components/TicketTable.tsx
"use client";
import React from "react";
import styles from "./table.module.scss";

interface Ticket {
  id: number
  titulo: string
  estado: string
  prioridad: string
  fecha: string
};

const tickets: Ticket[] = [
  { id: 1, titulo: "Error en login", estado: "Abierto", prioridad: "Alta", fecha: "2025-06-01" },
  { id: 2, titulo: "No carga perfil", estado: "Cerrado", prioridad: "Media", fecha: "2025-05-30" },
  { id: 3, titulo: "Solicitud nueva funcionalidad", estado: "Pendiente", prioridad: "Baja", fecha: "2025-05-28" },
];

export default function page() {
  return (
    <div className={styles.tableContainer}>
      <h2>Lista de Tickets</h2>
      <table>
        <thead>
          <tr>
            <th>ID</th>
            <th>TÃ­tulo</th>
            <th>Estado</th>
            <th>Prioridad</th>
            <th>Fecha</th>
          </tr>
        </thead>
        <tbody>
          {tickets.map((ticket) => (
            <tr key={ticket.id}>
              <td>{ticket.id}</td>
              <td>{ticket.titulo}</td>
              <td>{ticket.estado}</td>
              <td>{ticket.prioridad}</td>
              <td>{ticket.fecha}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

